import React from 'react'
import styles from './component.module.css'

export default function MainContent(maincontent) {
    return (
        <div className={styles.Mcomponent}>
            <h2>cognitive clouds react training</h2>
            <iframe className={styles.youtube} width="600" height="315" src="https://www.youtube.com/embed/bpVRWrrfM1M?start=1" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>"
            <h4 className={styles.styl}><strong>Declarative:</strong>
             React makes it painless to create interactive UIs.
             Design simple views for each state in your application, 
             and React will efficiently update and render just the right components when your data changes.
             Declarative views make your code more predictable and easier to debug.

            <strong>Component-Based:</strong>
            Build encapsulated components that manage their own state,
            then compose them to make complex UIs.
            Since component logic is written in JavaScript instead of templates, you can easily pass rich data through your app and keep state out of the DOM.</h4>
            <h2 className={styles.styl}>TO READ MORE ABOUT REACT PLEASE TAP ON THE REACT IN THE SIDEBAR MENU</h2>
            <h2 className={styles.styl}>TAP ON REDUX TO KNOW ABOUT REDUX</h2>
        </div>
    )
}
